html,
    body {
      margin: 0;
      padding: 0;
    }

    #renderCanvas {
      position: fixed;
      margin-top: 0px;
      width: 100%;
      height: 100%;
      object-fit: scale-down;
      background: lightblue;
      position: absolute;

      z-index: 0;
      /* margin: 48 0 48 0; */
    }

    .sidenav {
      height: 100%;
      width: 0;
      position: fixed;
      z-index: 1;
      top: 0;
      left: 0;
      background-color: #111;
      overflow-x: hidden;
      transition: 0.5s;
      padding-top: 60px;
    }

    .sidenav a {
      padding: 0px 8px 8px 32px;
      text-decoration: none;
      padding: 18px;
      width: 100%;
      color: #818181;
      display: block;
      transition: 0.3s;
    }

    .sidenav a:hover {
      color: #f1f1f1;
    }

    .sidenav .closebtn {
      position: absolute;
      top: 0;
      /* right: 25px; */
      font-size: 36px;
      color: #d6d0d0;
      margin-left: 0;
    }

    .accordion {
      background-color: #111;
      color: #818181;
      cursor: pointer;
      padding: 18px;
      width: 100%;
      border: none;
      text-align: left;
      outline: none;
      font-size: 15px;
      transition: 0.4s;
    }

    .accordion2 {
      background-color: #111;
